//  XHR  Promise ,
// config  endpoint, method, params, headers, responseType
const makeRequest = (config) => {
  return new Promise(function (resolve, reject) {
    const xhr = new XMLHttpRequest();
    const params = config.params;
    let paramString = ''
    let url = config.endpoint
    //  paramString  xhr.send  chrome 
    //  hack  paramString  url 
    if (params && typeof params === 'object') {
      paramString = Object.keys(params).map(function (key) {
        return encodeURIComponent(key) + '=' + encodeURIComponent(params[key]);
      }).join('&');
      url = `${config.endpoint}?${paramString}`
    }
    xhr.open(config.method, url, true);
    xhr.onload = function () {
      //  resolve
      //  reject
      if (this.status >= 200 && this.status < 300) {
        resolve(xhr.response);
      } else {
        reject({
          status: this.status,
          statusText: xhr.statusText
        });
      }
    };
    xhr.onerror = function () {
      reject({
        status: this.status,
        statusText: xhr.statusText
      });
    };
    //  header  zip  responseType
    if (config.headers) {
      Object.keys(config.headers).forEach(function (key) {
        xhr.setRequestHeader(key, config.headers[key]);
      });
    }
    if (config.responseType) {
      xhr.responseType = config.responseType
    }
    xhr.send();
  });
}
//  GET  wrapper, POST  GET
const get = (endpoint, config) => makeRequest({ ...config, method: 'GET', endpoint })
exports.get = get


